.app{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px 24px;
}
.app__searchForm{
    margin-top: 18px;
    ;
}
.app__input{
    padding: 6px 16px;
    min-width: 200px;
    max-width: 100vw;
    font-size: 15px;
}
.app__submit{
    padding: 6px 16px;
    font-size: 15px;
    margin-left: 6px;
    background-color: rgb(56, 192, 226);
    border: 2px solid rgb(56, 192, 226);
    border-radius: 4px;
    color: white;
    cursor: pointer;
}
.app__submit:hover{
    padding: 6px 16px;
    font-size: 15px;
    margin-left: 6px;
    background-color: white;
    border: 2px solid rgb(56, 192, 226);
    border-radius: 4px;
    color: rgb(56, 192, 226);
    cursor: pointer;
}
.app__recipes{
    display: grid;
    margin-top: 24px;
    grid-template-columns: 230px 230px;
    align-items: baseline;
}
.app__healthlabels{
    padding: 7px 8px;
    margin-left: 6px;
    border-radius: 4px;
}